import React, {useState, useEffect} from 'react';
import axios from 'axios';
import { Link } from "react-router-dom";

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';

import Url from "../api/url";

// Map config
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet'
import "leaflet/dist/leaflet.css"
import "leaflet/dist/images/marker-icon.png"
import L from 'leaflet'

delete L.Icon.Default.prototype._getIconUrl;

L.Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')
});
// end map config

function Groups(props) {
    const [catList, setCatList] = useState([]);
    const headers = {
        Authorization: `Token ${localStorage.getItem("token")}`,
      };  

      const getGroups = async() => {
        // axios.get(Url.baseUrl + `products/`, {headers: headers,})
        axios.get(Url.baseUrl + `groups/`, {headers: headers,})
            .then((response) => {     
                let res = response.data.results;
                console.log('res: ', res)
                setCatList(res)
            })
            .catch((error) => {
                console.log(error);
            })
    }

    useEffect(() => {     
        getGroups()
  }, []);

    return (
    <Container>
        <Row>
            <Col xs={12} md={8}>
            <div style={{border: 'black 1px solid', marginTop: '20px'}}>
                <MapContainer
                    center={[31.802143287509100, 35.398173127745430]}
                    zoom={11}
                    scrollWheelZoom={true}
                    style={{ height: '50vh', width: '100wh' }}
                    >
                    <TileLayer
                    attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                    />
                    <Marker position={[31.742143287509100, 35.318173127745430]}>
                        <Popup>
                            <h2>Bakhita</h2>
                        </Popup>
                    </Marker>
                    <Marker position={[31.772143287509100, 35.338173127745430]}>
                        <Popup>
                            <h2>Annuarite</h2>
                        </Popup>
                    </Marker>
                    <Marker position={[31.772143287509100, 35.308173127745430]}>
                        <Popup>
                            <h2>Mountain</h2>
                        </Popup>
                    </Marker>                    
                    <Marker position={[31.802143287509100, 35.398173127745430]}>
                        <Popup>
                            <h2>aaa</h2>
                        </Popup>
                    </Marker>  
                    <Marker position={[31.852143287509100, 35.358173127745430]}>
                        <Popup>
                            <h2>bbb</h2>
                        </Popup>
                    </Marker>  
                    <Marker position={[31.862143287509100, 35.298173127745430]}>
                        <Popup>
                            <h2>ccc</h2>
                        </Popup>
                    </Marker>                                          
                </MapContainer>
            </div>
            </Col>
            <Col xs={12} md={4}>
                    <h1 className="d-flex justify-content-center">Groups</h1>
                    {catList.map((item, index)=>{
                        return(
                            <div className="">
                                <Link to={`/group/${item.id}/`} className="link">
                                <h1 className="d-flex justify-content-center dark-row">{item.name}</h1>
                                </Link>
                            </div>
                        )
                    })}
            </Col>
        </Row>
    </Container>
);
}

export default Groups;
