import React, {useState, useEffect} from 'react';
import axios from 'axios';
import { Link, useParams } from "react-router-dom";

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';

import Url from "../api/url";

// Map config
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet'
import "leaflet/dist/leaflet.css"
import "leaflet/dist/images/marker-icon.png"
import L from 'leaflet'

delete L.Icon.Default.prototype._getIconUrl;

L.Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')
});
// end map config

function Group(props) {
    const params = useParams();
    const [peopleList, setPeopleList] = useState([]);
    const [group, setGroup] = useState([]);
    const [id, setId] = useState();
    const headers = {
        Authorization: `Token ${localStorage.getItem("token")}`,
      };  



    const getPeople = async() => {
        // axios.get(Url.baseUrl + `products/`, {headers: headers,})
        axios.get(Url.baseUrl + `people/?group=${params.id}`, {headers: headers,})
            .then((response) => {     
                let res = response.data.results;
                console.log('res: ', res)
                setPeopleList(res)
            })
            .catch((error) => {
                console.log(error);
            })
    }
    const getGroup = async() => {
      axios.get(Url.baseUrl + `groups/${params.id}`, {headers: headers,})
          .then((response) => {     
              let res = response.data;
              console.log('resgroup: ', res)
              setGroup(res)
          })
          .catch((error) => {
              console.log(error);
          })
  }
    useEffect(() => {     
        getPeople()
        getGroup()
        setId(params.id)
  }, []);

return (
<Container>
  <Row>
    <h1 className="d-flex justify-content-center">Group: {group.name}</h1>
  </Row>
  
  <Link to={`/groups/`} className='link'>
    <Button variant="success" style={{marginTop: "20px"}}>Back</Button> 
  </Link>
  
  <Row>
    <Col xs={12} md={8}>
      <Row>
        <Col>Description: {group.description}</Col>
      </Row>
      <Row>
        <Col>Population: {group.population}</Col>
        <Col>Families: {group.nunber_familes}</Col>
      </Row>
      <Row>
        <Col>
          <Table striped bordered hover>
          <thead>
            <tr>
              <th></th>
              <th>Female</th>
              <th>Male</th>
            </tr>
          </thead>
          <tbody>        
            <tr>
              <td>Children</td>
              <td>{group.children_female}</td>
              <td>{group.children_male}</td>
            </tr>  
            <tr>
              <td>Youth</td>
              <td>{group.youth_female}</td>
              <td>{group.youth_male}</td>
            </tr> 
            <tr>
              <td>Adults</td>
              <td>{group.adult_female}</td>
              <td>{group.adult_male}</td>
            </tr> 
            <tr>
              <td>Elderly</td>
              <td>{group.old_female}</td>
              <td>{group.old_male}</td>
            </tr>                               
          </tbody>
          </Table>
        </Col>
      </Row>    
      <Row>
        <Col>
        {group.lat && group.long &&
          <div style={{border: 'black 1px solid'}}>
          <MapContainer
            center={[group.lat, group.long]}
            zoom={13}
            scrollWheelZoom={true}
            style={{ height: '50vh', width: '100wh' }}
            >
            <TileLayer
              attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
              />
            <Marker position={[group.lat, group.long]}>
              <Popup>
                <h2>{group.name}</h2>
              </Popup>
            </Marker>
            <Marker position={[group.lat-1, group.long-1]}>
              <Popup>
                <h2>{group.name}</h2>
              </Popup>
            </Marker>
          </MapContainer>
          </div>
        }
        </Col>
      </Row>
      <Row style={{marginTop: '15px'}}>
        <Col>
        <iframe width="100%" height="500" src={`https://maps.google.com/maps?q=${group.lat}, ${group.long}&output=embed&z=12`}></iframe>
        </Col>
      </Row>
  
    </Col>
    <Col xs={12} md={4}>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>Name</th>
            <th>Surname</th>
          </tr>
        </thead>
        <tbody>        
          {peopleList.map((item, index)=>{
          return(
            <tr key={index}>
                    <td>
                        <Link to={`/people/${item.pk}`} className='link'>{item.name} {item.surname}</Link>
                    </td>
                    <td>{item.surname}</td>
                </tr>  
            )
          })}
        </tbody>
      </Table>
    </Col>
  </Row>
</Container>
);
}

export default Group;
